@import url(https://artsyui.netlify.app/src/artsyUI.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media screen and (max-width: 300px) {
  html {
    font-size: 30%;
  }
  .grid-4 {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* scroll bar styling */
.main::-webkit-scrollbar {
  width: 1rem;
  padding: 1rem;
}
.main::-webkit-scrollbar-thumb {
  background: var(--secondary-color);
  border-radius: 1rem;
}
.flex-align-center {
  display: flex;
  align-items: center;
}
.flex-col {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.gray {
  background: var(--gray-bg);
}
h1,
h2,
h3,
p {
  color: inherit;
}
img {
  width: 100%;
}
.grid-overlay {
  display: grid;
}
.grid-overlay > * {
  grid-row: 1/2;
  grid-column: 1/2;
}
.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}
.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}
.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.checkbox-text {
  margin: 0 1rem;
}
.btn span {
  margin: auto;
}

/* page layout */
.body {
  height: 100vh;
  width:100vw;
  background: var(--light-text);
  display: grid;
  grid-template-rows: auto 1fr;
  grid-template-areas:
    "header header"
    "main main  ";
}
.main {
  grid-area: main;
  overflow: auto;
}

.header {
  grid-area: header;
}


.page-header-section {
  display: grid;
}
.page-header-section i {
  color: var(--primary-color);
}
.section-bg {
  height: 100%;
  grid-column: 1/2;
  grid-row: 1/2;
}
.section-bg img {
  height: 100%;
}
.section-content {
  grid-column: 1/2;
  grid-row: 1/2;
  min-height: 16rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--light-text);
}
/* header  */
.arstyShop-header {
  padding: 2rem 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: var(--secondary-color);
}
.arstyShop-header > * {
  color: var(--light-text);
}
.right-side {
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
  font-size: large;
}
.right-side > i:hover {
  cursor: pointer;
  color: var(--primary-color);
}
.items {
  color: var(--primary-color);
  font-size: 1.4rem;
}
.user {
  cursor: pointer;
  position: relative;
}
.user:hover .user-dropdown {
  display: flex;
}
.user-dropdown {
  position: absolute;
  top: 5rem;
  background: var(--light-bg);
  color: var(--secondary-color);
  box-shadow: 0 0 10px -2px var(--secondary-text);
  width: 20rem;
  padding: 1rem 0;
  border-radius: 0.4rem;
  display: none;
}
.user-dropdown > * {
  align-self: stretch;
  padding: 1rem 1rem;
  justify-content: space-between;
}
.user-dropdown > *:hover {
  background: var(--secondary-color);
  color: var(--light-bg);
}


.hero-section {
  position: relative;
  width: 90vw;
  margin: auto;
}
.hero-section img{
  aspect-ratio: 16/9;
}
.hero-overlay {
  padding: 2rem;
  text-align: center;
  justify-content: center;
  grid-gap: 3rem;
  gap: 3rem;
  text-shadow: 2px 2px 5px var(--secondary-color);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  color: var(--light-text);
}
/* category section */
.category-section {
  padding: 2rem;
  margin-top: 3rem;
  overflow-y: auto;
  text-align: center;
}
.category-grid {
  grid-template-columns: repeat(auto-fill, minmax(min(20rem, 100%), 1fr));
  margin: 3rem;
}
.categories img {
  border-radius: 4px;
}
.category-name {
  background: var(--secondary-color);
  color: var(--light-text);
}

/* collection section  */
.collection-section {
  text-align: center;
  padding: 2rem;
  margin-top: 3rem;
}
.collection-cont {
  margin: 3rem;
}
@media screen and (max-width: 800px) {
  .collection-cont {
    grid-template-columns: 1fr;
  }
}
.collection-image img {
  object-fit: cover;
}
.collection-items {
  border-radius: 4px;
  overflow: hidden;
  display: grid;
  align-items: end;
}
.collection-image,
.collection-content {
  grid-column: 1/2;
  grid-row: 1/2;
  color: var(--light-text);
}
.collection-content {
  padding: 1rem;
  width: 100%;
  height: 100%;
  text-align: left;
  display: flex;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.356);
}
.collection-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.collection-tag {
  align-self: flex-start;
  background: var(--primary-text);
  color: var(--light-text);
}
.collection-artworks {
  justify-content: flex-end;
  align-items: flex-end;
  min-width: 50%;
}
.collection-artwork-item {
  width: 50%;
  border-radius: 4px;
  overflow: hidden;
}

/*  */
.menu-sec {
  background: var(--light-bg);
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  padding: 2rem 5%;
}
.main-drop {
  position: relative;
  padding: 1rem;
}
.dropdown-box {
  border-radius: 4px;
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
}
.main-drop .dropdown {
  background: var(--primary-text);
  color: var(--light-text);
}
.main-drop .dropdown > *:hover {
  background: var(--secondary-color);
}

/* dropdown */
.dropdown-container {
  position: relative;
  padding: 1rem;
}
.dropdown-btn {
  border: 1px solid var(--primary-text);
  border-radius: 3rem;
  padding: 1rem 2rem;
}
.dropdown-btn:hover {
  border-color: transparent;
  background: var(--gray-bg);
}
.dropdown {
  background: var(--light-bg);
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: -webkit-max-content;
  max-width: max-content;
  position: absolute;
  z-index: 1;
  top: 100%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  overflow: hidden;

  display: none;
}
.main-drop:hover .dropdown,
.dropdown-container:hover .dropdown {
  display: flex;
}
.dropdown > * {
  padding: 1rem 2rem;
  width: 100%;
  text-transform: uppercase;
}
.dropdown > *:hover {
  background: var(--gray-bg);
  cursor: pointer;
}

/* products section */
.products-sec {
  margin-top: 2rem;
}
.products-grid {
  padding: 2rem 5%;
  margin: 2rem 0;
  background: var(--light-bg);
}

.wishlist-sec {
  padding: 2rem 5%;
  margin: 2rem 0;
  background: var(--light-bg);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 5rem;
}
.wishlist-sec .favourite-icon {
  color: var(--light-bg);
  background: var(--primary-color);
}

